<template>
    <div>
<nav-bar :name="title"></nav-bar>
        <v-spacer></v-spacer> 
        <v-card class="ma-1" cols="12">
        <v-container>
            <v-row>
                <v-col cols="12">
                    <h5>Amount:</h5>
                <v-text-field
                outlined
                dense
                v-model="inv.amount"
                ></v-text-field>
                </v-col>
                <v-col cols="12">
                    <h5>Due date:</h5>
                <v-text-field
                label="aaaa-mm-dd"
                outlined
                dense
                v-model="inv.due_date"
                prepend-inner-icon="mdi-calendar"
                ></v-text-field>
                </v-col>
                <v-col cols="12">
                    <h5>Description:</h5>
                <v-text-field
                outlined
                dense
                v-model="inv.description"
                ></v-text-field>
                </v-col>
            </v-row>
            <v-col>
            <router-link to="/invoices">
            <v-btn
            class="mx-4 my-2"
            color="#989898"
            dark
            >
            GO BACK
            </v-btn>
            </router-link>
            <v-btn
            class="mx-4 my-2 primary"
            light
            :loading="loading"
            @click="update"
            >
            SAVE
            </v-btn>
            </v-col>
        </v-container>
        </v-card>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import InvoiceService from '@/services/InvoiceService';
import Swal from 'sweetalert2'

export default {
    components: {
      NavBar
    },

    data: () => ({
        title: 'UPDATE INVOICE',
        loading: false,
        inv: {
            amount: 0.0,
            due_date: '',
            description: '',
            status: 'P',
            store: ''
        }
    }),

    computed:{
        unique_id(){
            return this.$route.params.id
        }
    },

    mounted(){
        InvoiceService
            .get(this.unique_id)
            .then(res => {
                this.inv.amount = parseFloat(res.amount).toFixed(2);
                this.inv.due_date = res.due_date;
                this.inv.description = res.description;
                this.inv.status = res.status;
                this.inv.store = res.store.unique_id;
            })
            .catch(e => {
            /* 
            *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
            */

            Swal.fire({
            icon: 'error',
            title: 'Error',
            text: e.response.data['non_field_errors']
            });

        })
    },

    methods: {
        update(){
            this.loading = true;
            InvoiceService
                .update(this.inv, this.unique_id)
                .then(res => {
                    this.loading = false;
                    Swal.fire({
                    icon: 'success',
                    title: 'Success',
                    text: 'The invoices was successfully modified.'
                    }).then(() => {
                        this.$router.push('/invoices')
                    })
                })
                .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
               this.loading = false;
               console.log(e.response.data)
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data['non_field_errors']
                });
    
            })
        }
    }

}
</script>